/**
  Z-Index Management For Complex Layouts
  Usage: @include z($zelements, modals);
 */
.container {
  max-width: 540px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px; }
  .container:after {
    content: " ";
    display: block;
    clear: both; }
  @media (min-width: 768px) {
    .container {
      width: 720px;
      max-width: 100%; } }
  @media (min-width: 1024px) {
    .container {
      width: 960px; } }
  @media (min-width: 1440px) {
    .container {
      width: 1200px; } }
  @media (min-width: 1880px) {
    .container {
      width: 1800px; } }

@media (min-width: 1024px) {
  .container-desktop {
    width: 960px;
    margin: 0 auto; } }

@media (min-width: 1440px) {
  .container-desktop {
    width: 1200px; } }

@media (min-width: 1880px) {
  .container-desktop {
    width: 1800px; } }

.no-scroll {
  overflow: hidden; }

#root {
  height: 100%; }

html,
body,
main {
  height: 100%;
  font-family: 'Open Sans', sans-serif; }

a {
  text-decoration: none; }

* {
  box-sizing: border-box; }

*:before,
*:after {
  box-sizing: border-box; }
